import { FinishApprovalModalComponent } from './../finish-approval-modal/finish-approval-modal.component';
import { SignReminderModalComponent } from './../sign-reminder-modal/sign-reminder-modal.component';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { ICON_PATH } from './../../../../const/icon-path.const';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { OnlineSignatureComponent, Tab, MessageService } from 'merc-lib';
import { NzUploadFile } from 'ng-zorro-antd/upload/public-api';
import { SignCharacter } from './sign-character.enum';


interface SignTab extends Tab {
  signature: Blob;
  isAgree: boolean;
}
@Component({
  selector: 'app-approval-modal',
  templateUrl: './approval-modal.component.html',
  styleUrls: ['./approval-modal.component.scss']
})


export class ApprovalModalComponent implements OnInit {
  @ViewChild(OnlineSignatureComponent)
  signCompt!: OnlineSignatureComponent;


  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }

  validatedForm: FormGroup = this.fb.group({
    agree: [''],
    school: ['A']
  });

  // Tab start
  selectedIndex = 0;
  tabList: SignTab[] = [
    {
      id: SignCharacter.surpervisor,
      tabName: '推介主管',
      board: 'supervisor',
      signature: new Blob(),
      isAgree: false
    },
    {
      id: SignCharacter.sales,
      tabName: '育成人員',
      board: 'sales',
      signature: new Blob(),
      isAgree: false

    },
    {
      id: SignCharacter.viceSupervisor,
      tabName: '二代主管',
      board: 'viceSupervisor',
      signature: new Blob(),
      isAgree: false

    },
    {
      id: SignCharacter.director,
      tabName: '處經理',
      board: 'director',
      signature: new Blob(),
      isAgree: false
    },

  ];
  /** 是否全部簽名都有 */
  isAllSigned = false;

  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    public modalRef: NzModalRef,
    private modalService: NzModalService
  ) { }

  ngOnInit(): void {
  }


  /**
   * 送出所有資料
   */
  onSendClick(): void {
    const controls = this.validatedForm.controls;
    for (const i in controls) {
      if (controls.hasOwnProperty(i)) {
        controls[i].markAsDirty();
        controls[i].updateValueAndValidity();
      }
    }
    let isFinishedForm: boolean;
    // 全部填完送出審核
    if (this.isAllSigned && this.validatedForm.valid) {
      isFinishedForm = true;
      // TODO: 送出申請api完成後
      this.modalService.create({
        nzContent: FinishApprovalModalComponent,
        nzWidth: 600,
        nzMaskClosable: false,
        nzFooter: null,
        nzBodyStyle: { padding: '12px 24px' },
        nzComponentParams: { isFinishedForm }
      }).afterClose.subscribe(res => {
        if (res.closedApproval) {
          this.modalRef.close();
        }
      });
    } else {
      // 沒填完提醒他繼續填
      isFinishedForm = false;
      console.log(isFinishedForm);

      this.modalService.create({
        nzContent: FinishApprovalModalComponent,
        nzWidth: 600,
        nzMaskClosable: false,
        nzFooter: null,
        nzBodyStyle: { padding: '12px 24px' },
        nzComponentParams: { isFinishedForm }
      }).afterClose.subscribe(res => {
        // TODO:確定不繼續填，就關掉modal
        if (res.closedApproval) {
          this.modalRef.close();
        }
      });
    }
  }


  /**
   * 儲存簽名
   */
  onSaveSign(): void {
    this.tabList[this.selectedIndex].signature = this.signCompt.saveCanvas();
    // if (this.selectedIndex !== 0) {
    //   this.messageService.successModal(`您的推薦的<span class="text-secondary">學員王小明</span>
    // <span class="text-secondary">身分證字號A123456789</span>，
    // 已成功送出申請！`, { isPositiveBtn: true, imgPath: MODAL_ICON_PATH.SUCCESS, msgTitle: '推薦名單已送出', isHtmlCode: true });
    // }
    // 全部人都簽完顯示確認送出btn
    this.isAllSigned = this.tabList.every(tab => tab.signature.size !== 0);
  }

  /**
   * 點擊tab切換
   */
  onChangeTab(tabId: number): void {
    const selectedTab = this.tabList[tabId];
    this.selectedIndex = tabId;

    // 顯示儲存過的簽名
    if (selectedTab.signature.size !== 0) {
      const ctx = this.signCompt.context as CanvasRenderingContext2D;
      const src = window.URL.createObjectURL(selectedTab.signature);
      const img = new Image();
      img.src = src;
      img.onload = () => { ctx.drawImage(img, 0, 0); };
    }
  }

  onClearSign(signTab: SignTab): void {
    this.signCompt.clearCanvas();
    signTab.signature = new Blob();
  }


  /**
   * 顯示簽名板同意事項
   * @signTab 目前使用的簽名版
   */
  signAgreementModal(signTab: SignTab): void {
    signTab.isAgree = false;
    const modal = this.modalService.create({
      nzContent: SignReminderModalComponent,
      nzWidth: 600,
      nzMaskClosable: false,
      nzFooter: null,
      nzBodyStyle: { padding: '12px 24px' },
      nzComponentParams: { characterId: signTab.id }
    }).afterClose.subscribe(res => signTab.isAgree = res);
  }

  /**
   * 新增推薦人簽名格
   */
  onCheckReferee(check: boolean): void {
    if (check) {
      this.tabList.push({
        id: SignCharacter.referer,
        tabName: '推介人',
        board: 'referer',
        signature: new Blob(),
        isAgree: false
      });
    } else {
      this.tabList.pop();
    }
  }

  beforeUpload = (file: NzUploadFile): boolean => {
    console.log(file);
    return false;
  }
}
