import { ICON_PATH } from './../../../../const/icon-path.const';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { Component, Input, OnInit } from '@angular/core';

 enum ModalStatus {
  DONE = 'done',
  CONTINUE = 'continue'
}

@Component({
  selector: 'app-finish-approval-modal',
  templateUrl: './finish-approval-modal.component.html',
  styleUrls: ['./finish-approval-modal.component.scss']
})
export class FinishApprovalModalComponent implements OnInit {
  @Input() isFinishedForm?: boolean;
  modalStatus = ModalStatus;

  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  constructor(
    public modalRef: NzModalRef
  ) { }

  ngOnInit(): void {
  }

  /**
   * 送出申請
   */
  onSendClick(type: ModalStatus): void {
    if (type = this.modalStatus.CONTINUE) {
      this.modalRef.close({ closedApproval: false });
    } else {
      this.modalRef.close({ closedApproval: true });

    }
  }
}
