import { SignCharacter } from './../approval-modal/sign-character.enum';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ICON_PATH } from './../../../../const/icon-path.const';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { Component, Input, OnInit } from '@angular/core';
import { errorMsg } from 'projects/tc-lib/src/public-api';

@Component({
  selector: 'app-sign-reminder-modal',
  templateUrl: './sign-reminder-modal.component.html',
  styleUrls: ['./sign-reminder-modal.component.scss']
})
export class SignReminderModalComponent implements OnInit {
  @Input() characterId?: number;
  /** 顯示簽核提醒語 */
  showReminder = true;
  ICON_PATH = ICON_PATH;
  errorMsg = errorMsg;

  validateForm: FormGroup = this.fb.group({
    read: [null, Validators.required],
    know: [null],
    future: [null],
  });

  constructor(
    public modalRef: NzModalRef,
    private fb: FormBuilder
  ) { }

  ngOnInit(): void {
    switch (this.characterId) {
      case SignCharacter.director:
      case SignCharacter.referer:
      case SignCharacter.surpervisor:
      case SignCharacter.viceSupervisor:
        this.showReminder = true;
        break;
      default:
        this.showReminder = false;
    }
  }

  onSendClick(): void {
    const controls = this.validateForm.controls;
    for (const i in controls) {
      if (controls.hasOwnProperty(i)) {
        controls[i].markAsDirty();
        controls[i].updateValueAndValidity();
        if (!controls[i].value) { controls[i].setErrors({ msg: '此欄位為必填欄位' }) }
      }
    }
    if (this.validateForm.invalid) { return; }
    this.modalRef.close(true);
  }
}
